        <div class="b-good__bottom">
            <div class="b-good__content">
                <div class="b-good__title">{{ good.title }}</div>
                <div class="b-good__discription">{{ good.discription }}</div>
                <div class="b-good__price">${{ good.price }}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ["goods", "disabled"],
    emits: ["getJSON", "buyGood"],
    mounted() {
        this.getGoods()
    },
    methods: {
        getGoods() {
            this.$emit("getJSON", "/api/catalog" ,"goodsList")
        },
        buyGood(url, good){
            this.$emit("buyGood", url, good)
        },
    }
}

</script>
<style lang="scss">
.b-good {
    max-height: 579.39px;
    max-width: 359px;
    &__top {
        position: relative;
    }
    &__shadowBox {
        display: none;
    }
    &__top:hover &__shadowBox {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        color: white;
        background: rgba(58, 56, 56, 0.86);
    }
    &__text {
        padding-left: 11px;
    }
    &__img {
        display: block;
    }
    &__content {
        padding: 25px 17px;
    }
    &__title {
