        line-height: 16px;
    }
    &__discription {
        font-weight: 300;
        font-size: 14px;
        line-height: 17px;
        color: #5d5d5d;
        margin-top: 13px;
        max-height: 30px;
        overflow: auto;
    }
    &__price {
